"use client";
import { useState } from "react";
import { Card } from "@/components/ui/card";
import CashflowChart from "./components/cashflowChart.jsx ";
import TransactionTable from "./components/transactionTable";
import ToggleButton from "./components/toggleButton";
import { useCashflowData } from "./components/useCashflowData";
import { supabase } from "../../../lib/supabaseClient";

export default function Page() {
  const { transactions, chartData, loading, refetch } = useCashflowData();
  const [showTable, setShowTable] = useState(false);

  const handleAddTransaction = async (newTransaction) => {
  try {
    // Compute new balance
    const lastBalance = transactions.length
      ? transactions[transactions.length - 1].balance
      : 0;

    const newBalance =
      lastBalance + (newTransaction.deposit || 0) - (newTransaction.withdrawal || 0);

    const transactionToInsert = {
      ...newTransaction,
      balance: newBalance,
    };

    const { data, error } = await supabase
      .from("transactions")
      .insert([transactionToInsert]);

    if (error) {
      console.error("Failed to add transaction:", error.message);
      alert("Error adding transaction");
      return;
    }

    // Refetch data to update table and chart
    refetch();
  } catch (err) {
    console.error("Unexpected error:", err);
    alert("Failed to add transaction.");
  }
};

const handleDeleteTransaction = (index) => {
    // Remove the transaction at the given index
    const updated = transactions.filter((_, i) => i !== index);
    
    // Recalculate balances after deletion
    const recalculated = updated.map((t, i) => {
      const prevBalance = i > 0 ? updated[i - 1].balance : 0;
      return { ...t, balance: prevBalance + (t.deposit || 0) - (t.withdrawal || 0) };
    });

    setTransactions(recalculated);
  };


 

  return (
    <div className="p-6 space-y-6">
      <h1 className="text-2xl font-bold">ðŸ’° 2025 Cashflow Overview</h1>

      {/* ðŸ“Š Monthly Chart */}
      <Card>
        <CashflowChart data={chartData} />
      </Card>

      {/* ðŸ“‹ Toggle Transaction Table */}
      <div>
        <ToggleButton showTable={showTable} setShowTable={setShowTable} />
        {showTable && (
          <TransactionTable
            transactions={transactions}
            loading={loading}
            onAddTransaction={handleAddTransaction}
          />
        )}
      </div>
    </div>
  );
}
